.footer {
  height: 359px;
  position: sticky;
  bottom: 0;
  z-index: -1;
  background-image: url("../images/footer.png");
  background-size: cover;
  background-position: center;

  &__logo {
    position: absolute;
    bottom: 40px;
    left: 0;
    right: 0;
    margin: auto;
  }
}

@media (max-width: $mobilWidth) {
  .footer {
    height: 200px;

    &__logo {
      height: 50px;
      width: 77px;
    }
  }
}
